
// class Element cration de mon constructor
class Element {
  constructor (name, type, classname) {
    this.name = name
    this.type = type
    this.classname = classname
  }

  // fonction pour crer le type d'lment (div/p...) et le nom de classe puis retouner le nom
  createElt () {
    this.name = document.createElement(this.type) // on rcupre le nom et on lui cr un type (div/p/li etc...)
    this.name.classList.add(this.classname) // on rcupre le nom et on lui donne une classe (ex: class= "card__title")
    return this.name // on retoune le nom
  }

  // fonction pour rcuprer et utiliser createElt
  get elt () {
    return this.createElt()
  }
}

export { Element }
